import React, { useEffect, useState } from 'react'
import { Box, Button, Grid, Link } from '@material-ui/core';
import { useStyles } from './FeeStructureStyle';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { Typography } from '@mui/material';
import CardActions from '@mui/material/CardActions';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const FeeStructure = () => {
  const classes = useStyles();
  const history = useHistory();

  const [allFeeStructure, setAllFeeStructure] = useState([]);

  let unmounted = false;

  useEffect(() => {
    getAllFeeStructure();
  }, [])

  const getAllFeeStructure = async () => {
    unmounted = false;
    const source = axios.CancelToken.source();
    await axios.get(`${process.env.REACT_APP_SERVER}/fees-structure-info`)
      .then((response) => {
        console.log(response.data);
        setAllFeeStructure(response.data)

      })
      .catch((error) => {
        if (!unmounted) {
          if (error.request.status === 403) {
            localStorage.removeItem('userDetail');
            localStorage.removeItem('userToken');
            localStorage.removeItem('activeSubscription');
            history.replace('/login');
            history.go(0);
          }
        }
      })
      .finally(() => {
        return function () {
          unmounted = true;
          source.cancel("Cancelling in cleanup");
        };
      });
  }

  return (
    <React.Fragment>
      {JSON.parse(localStorage.getItem('userDetail'))?.profile?.name === 'Admin' ?
        (<Box component="span" className={classes.hideButtons}>
          <Grid container spacing={5}>
            <Grid xs={6} md={6} justifyContent='flex-end' className={classes.gridElement}>
              <Button variant='contained' size="medium" onClick={() => { history.push(`setup/add-fee`) }}>Add Fee</Button>
            </Grid>
          </Grid>
        </Box>) : <span></span>
      }
      <Box sx={{ width: '100%' }}>
        <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          {allFeeStructure.map(
            (fee) => {
              return (
                <Grid item key={fee._id}>
                  <Card style={{ minWidth: 300, width: 'auto', margin: 20 }}>
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="div">
                        {fee.name}
                      </Typography>
                      <Box style={{ margin: 20 }}>
                        <Grid container style={{ marginBottom: 10 }}>
                          <Grid >
                            <Typography style={{ paddingRight: 5 }} variant='body1' color="text.secondary">
                              Total Fees:
                            </Typography>
                          </Grid>
                          <Grid>
                            {fee?.details?.map(fee => { return fee.amount }).reduce((x, y) => parseFloat(x) + parseFloat(y))}
                          </Grid>
                        </Grid>
                      </Box>
                      <CardActions style={{ position: 'relative' }}>
                        <Link onClick={() => { history.push(`/setup/fee-details?feeId=${fee._id}`) }} variant='text' size="small" style={{ position: 'absolute', right: 0, top: 0, cursor: 'pointer' }}>Details <ArrowForwardIosIcon style={{ fontSize: 9, cursor: 'pointer' }} /></Link>
                      </CardActions>
                    </CardContent>
                  </Card>
                </Grid>
              )
            }
          )
          }
        </Grid>
      </Box>
    </React.Fragment>
  )
}

export default FeeStructure